export default new Vuex.Store({
  state: {
    contacts: [

      { "id": 1, "first_name": "Leticia", "last_name": "Hurlin", "email": "lhurlin0@eventbrite.com", "birsday": "1987-05-17", "website": "newsvine.com", "foto": "data:image/png;base64,", "number": "907-553-7584" },
      { "id": 2, "first_name": "Sherman", "last_name": "Eccleshall", "email": "seccleshall1@surveymonkey.com", "birsday": "1987-05-16", "website": "parallels.com", "foto": "data:image/png;base64,", "number": "426-238-5878" },
      { "id": 3, "first_name": "Morena", "last_name": "Cowburn", "email": "mcowburn2@smugmug.com", "birsday": "1987-05-18", "website": "creativecommons.org", "foto": "data:image/png;base64,", "number": "703-143-7112" },
      { "id": 4, "first_name": "Faustine", "last_name": "Krauze", "email": "fkrauze3@is.gd", "birsday": "1987-05-14", "website": "ehow.com", "foto": "data:image/png;base64,", "number": "259-197-4440" },
      { "id": 5, "first_name": "Kira", "last_name": "Bissell", "email": "kbissell4@examiner.com", "birsday": "1987-05-18", "website": "freewebs.com", "foto": "data:image/png;base64,", "number": "473-270-1283" },
      { "id": 6, "first_name": "Chico", "last_name": "Luscombe", "email": "cluscombe5@pagesperso-orange.fr", "birsday": "1987-05-19", "website": "pcworld.com", "foto": "data:image/png;base64,", "number": "590-713-4484" },
      { "id": 7, "first_name": "Binni", "last_name": "Jakubovitch", "email": "bjakubovitch6@cbc.ca", "birsday": "1987-05-20", "website": "weather.com", "foto": "data:image/png;base64,", "number": "584-810-4933" },
      { "id": 8, "first_name": "Brita", "last_name": "Vedyashkin", "email": "bvedyashkin7@woothemes.com", "birsday": "1987-05-10", "website": "meetup.com", "foto": "data:image/png;base64,", "number": "721-455-7498" },
      { "id": 9, "first_name": "Devonne", "last_name": "Pinnion", "email": "dpinnion8@artisteer.com", "birsday": "1987-05-1", "website": "php.net", "foto": "data:image/png;base64,", "number": "191-642-5777" },
      { "id": 10, "first_name": "Valli", "last_name": "Storm", "email": "vstorm9@businessinsider.com", "birsday": "1987-11-20", "website": "diigo.com", "foto": "data:image/png;base64,", "number": "791-197-5751" },
 
    ],
    search: "",
    sortMode: "first_name",
    filterName: "",
    contactTags: ["friends", "boss", "collegs"],

  },
  getters: {

    getContacts(state) {
      return state.contacts;
    },
    getContactById: state => id => {
      return state.contacts.find(c => c.id === id);
    },
    getSortContacts(state) {
      let sortFunction = {
        first_name(a, b) {
          if (a.first_name < b.first_name) {
            //console.log(-1);
            return -1;
          }
          if (a.first_name > b.first_name) {
            //console.log(1);
            return 1;
          }
          return 0;
        },
        last_name(a, b) {
          if (a.last_name < b.last_name)
            return -1;
          if (a.last_name > b.last_name)
            return 1;
          return 0;
        },
        birsday(a, b) {
          let adate = new Date(a.birsday)
          let bdate = new Date(b.birsday)
          if (adate.getMonth() < bdate.getMonth()) {
            return -1;
          }
          if (adate.getDate() < bdate.getDate()) {
            return -1;
          }
          if (adate.getMonth() > bdate.getMonth()) {
            return 1;
          }
          if (adate.getDate() > bdate.getDate()) {
            return 1;
          }
          return 0;
        }
      }
      let listSort = (sortName = "first_name") => {
        let mapped = state.contacts;
        let today = new Date();
        let m = today.getMonth();
        let d = today.getDate();
        if (sortName == 'birsday') {
          mapped = state.contacts.filter((e) => {
            if (e.birsday.split("-")[1] > m) {
              if (e.birsday.split("-")[2] < d) {
                return 0
              }
              return 1
            } else {
              return 0
            }
          })
        }
        return mapped.sort(sortFunction[sortName]);
      }

      let array = listSort(state.sortMode);
      // разделение по буквам 

      let char = "";
      array.forEach((e) => {
        e.hr = false
        if (char != e.first_name[0]) {
          // e.hr = !state.filerName ? true : false;
          e.hr = true
          char = e.first_name[0];
        }
      })

      if (state.contactTags.indexOf(state.filterName) !== -1) {
        array = array.filter((e) => {
          return e.tags?(e.tags.indexOf(state.filterName)!==-1?true:false):false
        })
      }
      return array;

    },
    getSearch(state, getters) {
      return getters.getSortContacts.filter(function (el) {
        return (el.first_name + " " + el.last_name).toLowerCase().indexOf(state.search.toLowerCase()) !== -1
      })
    },
    getLength(state) {
      return state.contacts.length
    },

  },

  mutations: {
    setSearchString: (state, payload) => {
      state.search = payload
    },
    addNew: (state, contact) => {
      console.log(contact.id, state.contacts.length);
      contact.id = state.contacts.length + 1
      console.log(contact.id, state.contacts.length);
      state.contacts.push(contact)
      console.log(state);
    },
    edit: (state, contact) => {
      let selectContact = state.contacts.find(c => c.id === contact.id);
      Object.keys(contact).map((e) => { selectContact[e] = contact[e] })
    },
    save: (state) => {
      localStorage.contacts = JSON.stringify(state.contacts)
    },
    remove:(state,id) => {
      state.contacts.splice(state.contacts.findIndex(c => c.id === id),1)
    },
    load: (state) => {
      try {
        state.contacts = JSON.parse(localStorage.contacts)
      } catch (e) {
        console.log('Контакты ещё не сохранены на этом ПК');
      }
    },
    nextSort: (state) => {
      let m = ["first_name", "last_name", "birsday"]
      let i = m.indexOf(state.sortMode) + 1;
      i = i >= m.length ? 0 : i
      state.sortMode = m[i]
    },
    nextFilter: (state) => {
      let i = state.contactTags.indexOf(state.filterName) + 1;
      i = i > state.contactTags.length ? 0 : i
      state.filterName = state.contactTags[i] ? state.contactTags[i] : ""
    }

  },
  actions: {

  },
  modules: {}
});
